import React, { useState, useEffect } from 'react';
import Layout from './layout'
import { Typography, Box, Avatar, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import api from '../../api';
import { useDispatch } from 'react-redux';
import { actions } from '../../redux/_actions';
import { AddShoppingCart, Language, People, Share, Storefront } from '@material-ui/icons';

const useStyles = makeStyles(() => ({
  tableBox: {
    maxHeight: '700px',
    overflowY: 'auto'
  },
  table: {
    width: '100%'
  },
  avatar: {
    color: 'white',
    backgroundColor: 'orangered'
  },
  tableWrapper: {
    marginTop: 50,
    display: 'flex',
    justifyContent: 'space-between',
    gap: '20px'
  },
  anlyticsWrapper: {
    marginTop: 25,
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    gap: '20px'
  },
  analytics: {
    width: '120px',
    height: '120px',
    textAlign: 'center',
    padding: '10px'
  },
  icon: {
    width: '40px',
    height: '40px'
  }
}));

const Analytics = () => {
  const [analytics, setAnalytics] = useState({});
  const [domains, setDomains] = useState([]);
  const [productNames, setProductNames] = useState([]);
  const classes = useStyles();

  const dispatch = useDispatch();

  useEffect(() => {
    fetchAnalytics();
  }, [])

  const fetchAnalytics = () => {
    dispatch(actions.setLoading(true));
    api.getAnalytics().then((data) => {
      dispatch(actions.setLoading(false));
      console.log(data)
      if (data.error) {
        // setError(data.error);
        console.log(data.error);
      } else {
        setAnalytics(data.analytics);
        setDomains(data.domains);
        setProductNames(data.productNames);
      }
    });
  }

  return (
    <Layout>
      <Box marginY={1}>
        <Typography variant='h4'>Analytics</Typography>
      </Box>
      <Box className={classes.anlyticsWrapper}>
        <Paper className={classes.analytics} elevation={3} >
          <Typography>Users</Typography>
          <People className={classes.icon} />
          <Typography>{analytics.userCount || 0}</Typography>
        </Paper>
        <Paper className={classes.analytics} elevation={3} >
          <Typography>Items</Typography>
          <Storefront className={classes.icon} />
          <Typography>{analytics.productCount || 0}</Typography>
        </Paper>
        <Paper className={classes.analytics} elevation={3} >
          <Typography>Shared</Typography>
          <Share className={classes.icon} />
          <Typography>{analytics.sharedProductCount || 0}</Typography>
        </Paper>
        <Paper className={classes.analytics} elevation={3} >
          <Typography>Purchase</Typography>
          <AddShoppingCart className={classes.icon} />
          <Typography>{analytics.purchasedProductCount || 0}</Typography>
        </Paper>
        <Paper className={classes.analytics} elevation={3} >
          <Typography>Sites</Typography>
          <Language className={classes.icon} />
          <Typography>{domains.length}</Typography>
        </Paper>
      </Box>
      <Box className={classes.tableWrapper}>
        <Box width='50%'>
          <Typography variant='h6'>Shopping Sites ({domains.length})</Typography>
          <TableContainer component={Paper} className={classes.tableBox}>
            <Table className={classes.table} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>Domain</TableCell>
                  <TableCell align="center">Count</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {domains.map((domain, idx) => (
                  <TableRow key={idx}>
                    <TableCell><Link href={domain._id} target='_blank'>{domain._id}</Link></TableCell>
                    <TableCell align="center">{domain.count}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
        <Box width='50%'>
          <Typography variant='h6'>Product Names ({productNames.length})</Typography>
          <TableContainer component={Paper} className={classes.tableBox}>
            <Table className={classes.table} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell>ProductName</TableCell>
                  <TableCell align="center">Count</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {productNames.map((productName, idx) => (
                  <TableRow key={idx}>
                    <TableCell>{productName._id}</TableCell>
                    <TableCell align="center">{productName.count}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      </Box>
    </Layout>
  )
}

export default Analytics;